$font-stack: -apple-system, BlinkMacSystemFont, Roboto, "Open Sans", "Helvetica Neue", sans-serif;

//@import "~bootstrap/scss/bootstrap";

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/Roboto-Regular.ttf);
}

body {
  //background-image: url("../img/background-img.jpg");
  background-color: black;
  background-size: cover;
  background-position: center;
}

.wrap-game {
  height: 1200px;
  display: flex;
  align-items: center;
  justify-content: center;

}
.game-block {

}
.game{
  display: flex;
  flex-flow: wrap;
  width: 820px;
  //background-image: url(../img/background-img.jpg);
  background-repeat: no-repeat;
  background-position: center;

}
img{
  width: 200px;
  height: 200px;
}


.scene {
  width: 200px;
  height: 260px;
  border: 1px solid #CCC;
  margin: 40px 0;
  perspective: 600px;
}

.card {
  width: 200px;
  height: 200px;
  transition: transform 1s;
  transform-style: preserve-3d;
  cursor: pointer;
  position: relative;
  border:1px solid
}

.card.is-flipped {
  transform: rotateY(180deg);
}

.card__face {
  position: absolute;
  width: 100%;
  height: 100%;
  line-height: 260px;
  text-align: center;
  font-weight: bold;
  font-size: 40px;
  backface-visibility: hidden;
}

.card__face--front {
  background: red;
}

.card__face--back {
  background: blue;
  transform: rotateY(180deg);
}

.hidden{
  display:none;
}

.cromulon{
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 900;
  transition: top 2s;
  width: 600px;
  height: 600px;
  visibility: hidden;

}
.cromulon.activated{
  display: block;
  top: 24%;
  transition: all 2s;

}

.button {
  font-family: Helvetica;
  font-size: 1.5em;
  border: 5px solid #21fd09;
  padding: 15px;
  position: absolute;
  z-index: 500;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 1;
  p{
    color:#21fd09;
  }
}
.button.hidden{
  opacity: 0;
  transition: all 2s;
}
.start-animation{
  animation: fadeIn 5s ;
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.end-animation{
  animation: fadeAut 3s ;
}
@keyframes fadeAut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
.end-game{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column-reverse;
  position: absolute;
  top: 50%;
  left: 35%;
  font-family: Helvetica;
  font-size: 1.5em;
  border: 5px solid #21fd09;
  padding: 15px;

  z-index: 500;

  opacity: 1;
  p{
    color:#21fd09;
  }
}

.button-restart {
  color:#21fd09;
  font-size: 20px;
}

